@import './fonts.scss';

body {
    font-family: 'Greycliff CF';
}

* {
    box-sizing: border-box;
}

.program-section {
    height: 100vh;

    .container {
        max-width: 1450px;
        width: 100%;
        margin: 0 auto;
        padding-left: 16px;

        .program-content {
            display: flex;

            .found-wrapper {
                width: 50%;
                display: flex;
                align-items: center;
                justify-content: center;

                .found-content {
                    width: 100%;
                    max-width: 550px;

                    .found-title {
                        margin-bottom: 24px;
                        font-weight: 700;
                        font-size: 62px;
                    }
                    .found-description {
                        font-family: 'Quincy CF';
                        font-size: 20px;
                        font-weight: 400;
                        margin-bottom: 38px;
                        line-height: 150%;
                    }
                    .swiper-pagination {
                        display: block;
                        transform: translate3d(0,0,0);
                        text-align: left;

                        .swiper-pagination-bullet {
                            display: inline-block;
                            padding: 0;
                            border-radius: 100px;
                            width: auto;
                            height: auto;
                            margin: 5px;
                            text-align: center;
                            font-size: 182px;
                            color:#fff;
                            opacity: 1;
                            background-color: rgba(250, 250, 250);

                            .found-tab {
                                padding: 12px 24px 12px 12px;
                                display: flex;
                                align-items: center;
                                border: 1px solid rgb(255, 255, 255);
                                border-radius: 100px;
                                background-color: rgb(246, 246, 246, 0);
                                opacity: 0.6;
                                cursor: pointer;
    
                                .found-tab-icon {
                                    margin-right: 10px;
                                    width: 32px;
                                    height: 32px;
                                    background-color: rgb(255, 255, 255, 0);
                                }

                                .found-tab-text {
                                    font-size: 18px;
                                    font-weight: 400;
                                    background-color: rgb(255, 255, 255, 0);
                                }

                                &:hover {
                                    border: 1px solid #ddddd0;
                                }
                            }
                        }
                        .swiper-pagination-bullet-active {
                            .found-tab {
                                opacity: 1;

                                .found-tab-text {
                                    font-weight: 700;
                                }
                            }
                            &:nth-child(1) {
                                .found-tab {
                                    background-color: rgba(90, 88, 207, 0.149);
                                    
                                }
                            }
                            &:nth-child(2) {
                                .found-tab {
                                    background-color: rgba(64, 139, 252, 0.149);
                                    
                                }
                            }
                            &:nth-child(3) {
                                .found-tab {
                                    background-color: rgba(38, 134, 83, 0.2);
                                    
                                }
                            }
                            &:nth-child(4) {
                                .found-tab {
                                    background-color: rgba(249, 210, 112, 0.251);
                                    
                                }
                            }
                            &:nth-child(5) {
                                .found-tab {
                                    background-color: rgba(251, 202, 202, 0.302);
                                    
                                }
                            }
                        }
                    }
                }
                .swiper-pagination {
                    position: static;
                    display: block; 
                }
            }
            .program-list-wrapper {
                width: 50%;
                height: 100vh;
                position: relative;

                .desktop-background {
                    position: fixed;
                    display: block;
                    z-index: 0;
                    height: 100vh;
                    object-fit: fill;
                }
                .mobile-background {
                    display: none;
                }
                .swiper { 
                    height: 100vh;
                    z-index: 10;
                    
                    .program-list {
                        .swiper-slide {
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            justify-content: center;

                            .program-card {
                                max-width: 452px;
                                text-align: center;
                                background-color: #fff;
                                padding: 48px;
                                border-radius: 10px;
                                box-shadow: 0px 20px 40px rgba(47, 47, 47, 0.2);

                                .title-wrapper {
                                    display: flex;
                                    align-items: center;
                                    margin-bottom: 24px;
    
                                    .title-icon {
                                        margin-right: 14px;
                                    }
                                    .program-item-title {
                                        font-size: 28px;
                                        margin: 0;
                                        font-weight: 700;
                                    }
                                }
                                .prescription-description {
                                    line-height: 170%;
                                    font-size: 18px;
                                    font-weight: 400;
                                    margin-bottom: 24px;
                                    color: #10100E;
                                    text-align: left;
                                }
                                .prescription-list {
                                    display: flex;
                                    padding: 0;
    
                                    .prescription-item {
                                        display: flex;
                                        align-items: center;
                                        background-color: #FDF4DB;
                                        padding: 12px 15px 12px 12px;
                                        border-radius: 8px;
    
                                        .prescription-topic {
                                            font-size: 14px;
                                            font-weight: 600;
                                            display: block;
                                        }
                                        .prescription-icon {
                                            margin-left: 8px;
                                        }
                                        &:nth-child(2) {
                                            margin: 0 14px;
                                        }
                                    }
                                }
                                .card-list {
                                    margin-bottom: 40px;
                                    list-style: none;
                                    padding: 0;
    
                                    .card-item {
                                        margin-bottom: 16px;
                                        text-align: left;
                                        display: flex;
                                        align-items: flex-start;
    
                                        .card-item-icon {
                                            margin: 0 13px 0 8px;
                                            transform: translate(0, 5px);
                                        }
                                        .card-item-text {
                                            font-size: 18px;
                                            font-weight: 400;
                                            line-height: 26px;
                                            margin: 0;
                                        }
                                    }
                                }
                                .card-button {
                                    background-color: #10100E;
                                    border-radius: 5px;
                                    color: #fff;
                                    font-size: 16px;
                                    font-weight: 600;
                                    padding: 13px 0;
                                    width: 100%;
                                    border: 0;
                                    margin-bottom: 24px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

